import 'core-js/features/map'
import 'core-js/features/set'
import React from 'react'
import ReactDOM from 'react-dom'
import bridge from '@vkontakte/vk-bridge'
import App from './App'

import gallery from './img/gallery.png'
import url from './img/url.png'
import intro1 from './img/intro1.png'
import intro2 from './img/intro2.png'
import ava from './img/ava.png'

import jpeg from './img/jpeg.png'
import png from './img/png.png'
import heic from './img/heic.png'
import pdf from './img/pdf.png'
import gif from './img/gif.png'
import tiff from './img/tiff.png'
import wbmp from './img/wbmp.png'

ReactDOM.render(<App />, document.getElementById('root'))
// if (process.env.NODE_ENV === "development") {
  import('./eruda').then(({ default: eruda }) => {})
// }
// принудительная загрузка фото
if (window.Image) {
  new window.Image().src = gallery
  new window.Image().src = url
  new window.Image().src = intro1
  new window.Image().src = intro2
  new window.Image().src = ava
  new window.Image().src = wbmp
  new window.Image().src = jpeg
  new window.Image().src = png
  new window.Image().src = heic
  new window.Image().src = pdf
  new window.Image().src = gif
  new window.Image().src = tiff
}
bridge.send('VKWebAppInit')